.reviews-section {
  background-color: #fff;
}

.reviews-title {
  font-size: 36px;
  text-align: center;
  line-height: 50px;
  margin-bottom: 55px;
}

.review-slider {
  max-width: 970px;
  margin: 0 auto;
}

.review-slider .owl-dots {
  text-align: center;
  margin-top: 20px;
}

.review-slider .owl-dots button {
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background-color: #E9E9E9;
  margin-right: 6px;
}

.review-slider .owl-dots button:last-child {
  margin-right: 0;
}

.review-slider .owl-dots button.active {
  background-color: #23A8CD;
}

.review-slider .owl-dots button:focus {
  outline: none;
}

.review-slider .owl-nav button {
  display: block;
  width: 56px;
  height: 56px;
  border-radius: 50%;
  background: #F6F6F6 url("../img/arrow-left-blue.svg") center no-repeat !important;
  background-size: 40px;
  position: absolute;
  right: -100px;
  top: 70px;
}

.review-slider .owl-nav button span {
  display: none;
}

.review-slider .owl-nav button:focus {
  outline: none;
}

.review-slider .owl-nav button.owl-next {
  transform: rotate(180deg);
}

.review-slider .owl-nav button.owl-next:hover {
  background: #23A8CD url("../img/arrow-right-white.svg") center no-repeat !important;
  background-size: 40px;
  transform: rotate(0);
}

.review-slider .owl-nav button.owl-prev {
  top: 130px;
}

.review-slider .owl-nav button.owl-prev:hover {
  background: #23A8CD url("../img/arrow-right-white.svg") center no-repeat !important;
  background-size: 40px;
  transform: rotate(180deg);
}

.review-slider .slider__item {
  padding: 55px 130px;
  background-color: #F6F6F6;
  border-radius: 4px;
  position: relative;
}

.review-slider .slider__item:before {
  display: block;
  content: '';
  width: 32px;
  height: 32px;
  background: transparent url("../img/quatermark.svg") center no-repeat;
  background-size: contain;
  position: absolute;
  top: 50px;
  left: 75px;
}

.review-slider .slider__item p {
  font-size: 17px;
  line-height: 26px;
  margin-bottom: 15px;
}

.review-slider .slider__item h4 {
  font-size: 22px;
  font-weight: 500;
  color: #23A8CD;
}

@media screen and (max-width: 1200px) {
  .review-slider {
    max-width: 770px;
  }
  .review-slider .slider__item {
    padding: 55px 100px;
  }
  .review-slider .slider__item:before {
    left: 60px;
  }
}

@media screen and (max-width: 990px) {
  .review-slider .owl-nav {
    display: none;
  }
}

@media screen and (max-width: 500px) {
  .reviews-section .container {
    width: 100%;
    padding: 0;
  }
  .reviews-section .container .row {
    margin: 0;
  }
  .reviews-section .container .col-md-12 {
    padding: 0;
  }
  .reviews-title {
    font-size: 26px;
    line-height: 36px;
    max-width: 210px;
    margin: 0 auto;
    margin-bottom: 25px;
  }
  .review-slider .owl-dots {
    margin-top: 15px;
  }
  .review-slider .slider__item {
    padding: 75px 20px 25px;
  }
  .review-slider .slider__item:before {
    top: 30px;
    left: 20px;
  }
  .review-slider .slider__item p {
    font-size: 16px;
    line-height: 23px;
    margin-bottom: 15px;
  }
  .review-slider .slider__item h4 {
    font-size: 21px;
    line-height: 30px;
  }
}

